	import common from '../../../common/common.js'
	
	export default {
		data() {
			return {
				userInfo:[],
				userid:'',
				invite:[],
				info:{},
				
				titleHeight:0,
				statusBarHeight:0,
				naviBarHeight:0,
			};
		},
		onLoad() {
			var that = this
			var userid = uni.getStorageSync('userid');
			var userInfo = uni.getStorageSync('userInfo');
			that.userInfo = userInfo;
			that.userid = userid;
			
			var currentPages = getCurrentPages()
			that.info.delta = currentPages.length
			
			that.titleHeight = common.titleHeight;
			that.statusBarHeight = common.statusBarHeight;
			that.naviBarHeight = common.naviBarHeight;
			
			that.getInviteInfo();
		},
		onShow(){
			var that = this
			var userid = uni.getStorageSync('userid');
			var userInfo = uni.getStorageSync('userInfo');
			that.userInfo = userInfo;
			that.userid = userid;
		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');	
			that.getInviteInfo();
			
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		methods:{
			//收货地址
			address(){
				var that = this
				uni.navigateTo({
					url: '/pages/user/setting/address',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//获取诉请人信息
			getInviteInfo(){
				var that = this
				var userInfo = uni.getStorageSync('userInfo');
				var invite_uid = userInfo.invite_uid
				uni.request({
					url: common.apiUrl + 'user/getInviteInfo',
					method: 'GET',
					data: {
						invite_uid:invite_uid
					},
					success: res => {
						var data = res.data
						that.invite = data.invite
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//推荐人
			goinvite(){
				var that = this
				uni.navigateTo({
					url: '/pages/user/setting/invite',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			
			
			//修改昵称
			modNickname(){
				var that = this
				uni.navigateTo({
					url: '/pages/user/setting/nickname',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//修改头像
			changeAvatar(){
				var that = this
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						//console.log(JSON.stringify(res.tempFilePaths));
						const tempFilePaths = res.tempFilePaths;
						console.log(tempFilePaths);
						//return false;
						console.log(that.userid)
						uni.uploadFile({
							url: common.apiUrl+'user/saveUserAvatar', //仅为示例，非真实的接口地址
							filePath: tempFilePaths[0],
							name: 'file',
							formData: {
								'userid': that.userid
							},
							success: (uploadFileRes) => {
								//console.log(uploadFileRes.data);
								var jsonDataStr = JSON.parse(uploadFileRes.data);
								var userInfo = uni.getStorageSync('userInfo');
								console.log(that.userInfo)
								userInfo['users_avatar'] = jsonDataStr.uploadfile;
								uni.setStorageSync('userInfo',userInfo);
								that.userInfo = userInfo;
// 								
// 								console.log(that.userInfo)
							}
						});
					}
				});
			},
			
			
			//退出当前账号
			userLoginQuit(){
				var that = this
				
				
				
				uni.showModal({
					title: '提示',
					content: '确定要退出当前账号吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						console.log(res)
						if(res.confirm){
							uni.removeStorageSync('userid');
							uni.removeStorageSync('userInfo');
							uni.showLoading();
							uni.reLaunch({
								url: '/pages/user/user',
								success: res => {
									uni.hideLoading();
								},
								fail: () => {},
								complete: () => {}
							});
						}
						
											
					},
					fail: () => {},
					complete: () => {}
				});
			},
		}
	}
